<template>
  <div class="col-md-6 col-lg-4">
    <div class="card mb-4">
      <img
        class="card-img-top"
        :src="restaurant.image"
        alt="Card image cap"
        width="286px"
        height="180px"
      />
      <div class="card-body">
        <p class="card-text title-wrap">
          <router-link
            :to="{ name: 'restaurant', params: { id: restaurant.id } }"
            >{{ restaurant.name }}</router-link
          >
        </p>
        <span class="badge badge-secondary">{{
          restaurant.Category.name
        }}</span>
        <p class="card-text text-truncate">
          {{ restaurant.description }}
        </p>
      </div>
      <div class="card-footer">
        <button
          type="button"
          class="btn btn-danger btn-border favorite mr-2"
          @click.stop.prevent="deleteFavorite(restaurant.id)"
          v-if="restaurant.isFavorited"
          :disabled="isProcessing"
        >
          移除最愛
        </button>
        <button
          type="button"
          class="btn btn-primary btn-border favorite mr-2"
          @click.stop.prevent="addFavorite(restaurant.id)"
          v-else
          :disabled="isProcessing"
        >
          加到最愛
        </button>
        <button
          type="button"
          class="btn btn-danger like mr-2"
          @click.stop.prevent="deleteLike(restaurant.id)"
          v-if="restaurant.isLiked"
          :disabled="isProcessing"
        >
          Unlike
        </button>
        <button
          type="button"
          class="btn btn-primary like mr-2"
          @click.stop.prevent="addLike(restaurant.id)"
          v-else
          :disabled="isProcessing"
        >
          Like
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import usersAPI from "../apis/users.js";
import { Toast } from "../utils/helpers.js";
export default {
  props: {
    initialRestaurant: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      restaurant: this.initialRestaurant,
      isProcessing:false
    };
  },
  methods: {
    async addFavorite(restaurantId) {
      try {
        this.isProcessing=true;
        await this.tryAddFavorite(restaurantId);
        this.isProcessing=false;
      } catch (e) {
        console.log(e);
        this.isProcessing=false;
        Toast.fireError("無法將餐廳加入最愛，請稍後再試",)
      }
    },
    async tryAddFavorite(restaurantId) {
      const response = await usersAPI.addFavorite({ restaurantId });
      console.log(response);
      if (response.statusText !== "OK") {
        throw new Error("Not Fetch UserCard");
      }
      this.restaurant = {
        ...this.restaurant,
        isFavorited: true,
      };
    },
    async deleteFavorite(restaurantId) {
      try {
        this.isProcessing=true;
        await this.tryDeleteFavorite(restaurantId);
        this.isProcessing=false;
      } catch (e) {
        this.isProcessing=false;
        console.log(e);
        Toast.fireError("無法將餐廳移除最愛，請稍後再試")
      }
      this.restaurant = {
        ...this.restaurant,
        isFavorite: false,
      };
    },
    async tryDeleteFavorite(restaurantId) {
      console.log(restaurantId);
      const response = await usersAPI.deleteFavorite({ restaurantId });
      if (response.statusText !== "OK") {
        new Error("Not Fetch UserCard");
      }
      this.restaurant = {
        ...this.restaurant,
        isFavorited: false,
      };
    },
    async addLike(restaurantId) {
      try {
        this.isProcessing=true;
        await this.tryAddLike(restaurantId);
        this.isProcessing=false;
      } catch (e) {
        this.isProcessing=false;
        console.log(e);
        Toast.fireError("無法加入喜愛，請稍後再試")
      }
    },
    async tryAddLike(restaurantId) {
      const response = await usersAPI.addLike({ restaurantId });
        console.log(response)
      if (response.statusText !== "OK") {
        throw new Error("can fetch UserCard");
      }
      this.restaurant = {
        ...this.restaurant,
        isLiked: true,
      };
    },
    async deleteLike(restaurantId) {
      try{
        this.isProcessing=true;
        await this.tryDeleteLike(restaurantId)
        this.isProcessing=false;
      }catch(e){
        this.isProcessing=false;
        console.log(e)
        Toast.fireError('無法移出喜愛，請稍後再試')
      }
    },
    async tryDeleteLike(restaurantId){
      const response = await usersAPI.deleteLike({ restaurantId });
        console.log(response)
      if (response.statusText !== "OK") {
        throw new Error("can fetch UserCard");
      }
      this.restaurant = {
        ...this.restaurant,
        isLiked: false,
      };
    }
  },
};
</script>
